<template>
  <div class="shadow rounded">
    <b-modal id="ModalTemporadaLista">
      <template v-slot:modal-title>
        <h5>¿Modificar Temporada?</h5>
      </template>
      <b-form @submit.prevent="">
        <b-form-group
          id="input-group-temporada-modal"
          label="Titulo"
          label-for="temporada-modal"
        >
          <b-form-input
            id="temporada-modal"
            type="text"
            v-model="titulo"
          >
          </b-form-input>
        </b-form-group>
        <b-form-group
          id="input-group-sinopsis"
          label="Sinopsis"
          label-for="sinopsis"
        >
          <b-form-textarea
            id="sinopsis"
            v-model="sinopsis"
            placeholder="Ingrese sinopsis..."
            rows="3"
            required
            max-rows="6"
          ></b-form-textarea>
        </b-form-group>
      </b-form>
      <template v-slot:modal-footer="{ ok, cancel }">
        <b-button
          size="sm"
          variant="success"
          @click="
            ok();
            modificarTemporada();
          "
        >
          Modificar
        </b-button>
        <b-button
          size="sm"
          variant="danger"
          @click="cancel()"
        >
          Cancelar
        </b-button>
      </template>
    </b-modal>
    <b-modal
      id="confirmacionTemporada"
      :title="`¿Esta seguro que desea eliminar ${titulo}?`"
    >
      <h6>Esta accion eliminara {{titulo}} de la base de datos</h6>
      <div>
        <b-button
          @click="personalizar = !personalizar"
          size="sm"
          class="float-right"
          variant="link"
        >
          Personalizar
        </b-button>
        <b-form-checkbox
          v-model="eliminacionTotal"
          switch
          value="accepted"
          unchecked-value="not_accepted"
        >
          Eliminar todos los datos asociados.
        </b-form-checkbox>
        <b-list-group v-if="personalizar">
          <b-list-group-item>
            <b-form-checkbox
              v-model="eliminacionTotal"
              switch
              value="accepted"
              unchecked-value="not_accepted"
            >
              Eliminar los comentarios
            </b-form-checkbox>
          </b-list-group-item>
          <b-list-group-item>
            <b-form-checkbox
              v-model="eliminacionTotal"
              switch
              value="accepted"
              unchecked-value="not_accepted"
            >
              Eliminar los capitulos.
            </b-form-checkbox>
          </b-list-group-item>
        </b-list-group>
      </div>
      <template v-slot:modal-footer="{ ok, cancel }">
        <b-button
          size="sm"
          variant="success"
          @click="
            ok();
            eliminarTemporada();
          "
        >
          Confirmar
        </b-button>
        <b-button
          size="sm"
          variant="danger"
          @click="cancel()"
        >
          Cancelar
        </b-button>
      </template>
    </b-modal>
    <b-list-group>
      <div
        v-for="(temporada, index) in temporadas"
        :key="temporada.id"
        :class="['mb-1', 'py-1', active === index ? 'bg-light' : '']"
      >
        <div class="d-flex my-2">
          <h5 class="ml-5 mt-3">Temporada {{ index + 1 }}</h5>
          <b-overlay
            v-if="editable && active === index"
            :show="cargando"
            rounded
            opacity="0.6"
            spinner-small=""
            spinner-variant="primary"
            class="d-inline-blocks ml-auto mr-2 mt-2"
          >
            <b-button-group>
              <b-button
                variant="outline-warning"
                id="edit"
                size="sm"
                v-b-modal.ModalTemporadaLista
              ><i class="fas fa-edit fa-sm"></i>
              </b-button>
              <b-button
                id="trash"
                variant="outline-danger"
                size="sm"
                v-b-modal.confirmacionTemporada
              ><i class="fas fa-trash-alt"></i></b-button>
            </b-button-group>
          </b-overlay>
        </div>
        <b-list-group-item
          href="#"
          @click="selContenido(temporada.id, index)"
        >
          <b-table
            stacked
            :items="items(temporada)"
          ></b-table>
        </b-list-group-item>
      </div>
    </b-list-group>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapGetters } from "vuex";
import notificacion from "@/mixin/notificacion";
export default {
  name: "ListaTemporada",
  mixins: [notificacion],
  props: {
    temporadas: {
      type: Array,
      default: null
    },
    seleccionable: {
      type: Boolean,
      default: true
    },
    editable: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      state: "not_accepted",
      active: null,
      titulo: "",
      sinopsis: "",
      id: "",
      eliminacionTotal: false,
      personalizar: false

    };
  },
  computed: {
    ...mapGetters("Catalogo", ["contenido"]),
    ...mapGetters(["cargando"]),
    getTemporada() {
      return {
        id_serie: this.contenido.id,
        titulo: this.titulo,
        sinopsis: this.sinopsis,
        id: this.id
      };
    }
  },
  methods: {
    ...mapMutations("Catalogo", ["updateTemporada"]),
    ...mapActions("Catalogo", [
      "updateCapitulos",
      "modifyTemporada",
      "deleteTemporada",
      "updateTemporadas"
    ]),
    selContenido(id, index) {
      this.active = index;
      this.updateTemporada(this.temporadas[index]);
      this.updateCapitulos(id);
      this.titulo = this.temporadas[index].titulo;
      this.sinopsis = this.temporadas[index].sinopsis;
      this.id = this.temporadas[index].id;
    },
    items(temporada) {
      return [
        {
          Titulo: temporada.titulo,
          Sinopsis: temporada.sinopsis,
          Capitulos: temporada.nroCapitulos
        }
      ];
    },
    async modificarTemporada() {
      const resultado = await this.modifyTemporada(this.getTemporada);
      this.show(resultado, "temporada", "modificada");
    },
    async eliminarTemporada() {
      const resultado = await this.deleteTemporada(this.getTemporada.id);
      if (resultado) this.updateTemporadas(this.contenido.id);
      this.show(resultado, "temporada", "eliminada");
    }
  }
};
</script>

<style lang="scss" scoped>
</style>