<template>
  <div>
    <b-list-group>
      <b-list-group-item
        v-for="(contenido, index) in contenidos"
        :key="index"
        href="#"
        @click="seleccionarContenido(contenido.id, index)"
        :class="[selContenido === contenido.id ? 'shadow' : '', 'sala', selContenido === contenido.id ? 'border-secondary' : '', selContenido === contenido.id ? 'bg-light' : '', 'd-flex item', 'mb-1']"

      >
        <div>
          <b-img
            :src="`/uploads/portadas/${contenido.portada}`"
            fluid
            alt="portada"
            class="image"
          ></b-img>
        </div>
        <div class="ml-4">
          <div>
            <h5>{{ contenido.Titulo }}</h5>
          </div>
          <div>
            <h6>{{ contenido.nTemp }}</h6>
          </div>
        </div>
      </b-list-group-item>
    </b-list-group>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapGetters } from "vuex";
export default {
  name: "ListaContenido",
  props: {
    contenidos: {
      type: Array,
      default: null
    },
    tContenido: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      state: "not_accepted",
    };
  },
  computed: {
    ...mapGetters("Catalogo", ["selContenido"])
  },
  methods: {
    seleccionarContenido(id, index) {
      this.updateContenido(this.contenidos[index]);
      this.updateTemporadas(this.contenidos[index].id);
      this.updateSelContenido(id);
    },
    ...mapActions("Catalogo", ["updateTemporadas"]),
    ...mapMutations("Catalogo", ["updateContenido", "updateSelContenido"]),
  },
  beforeDestroy () {
    this.updateSelContenido(null);
  },
};
</script>

<style lang="scss" scoped>
.image {
  height: 5rem;
}
.check {
  padding-top: 1.7rem;
}
</style>